import React from 'react';
import styles from './Contacts.module.css'
import {AxiosInstance as axios} from "axios";

function Contacts(props) {

    const sendForm = (formData) => {
        axios.post('https://smtp-server-nodejs.herokuapp.com/sendMessage', formData)
        props.clearForm()
    }

    return (
        <div className={styles.contacts}>
            <div className={styles.contactsTitle}>
                <span>Contacts</span>
            </div>
            <div className={styles.container}>
                <form className={styles.contactsForm}>
                    <input className={styles.inputForm} type="text" placeholder={"  Name"} name='name'/>
                    <input className={styles.inputForm} type="text" placeholder={"  Email"} contacts='contacts'/>
                    <textarea type="text" placeholder={"  Your message..."} name='message'></textarea>
                    <button className={styles.buttonForm} onSubmit={sendForm}>SEND MESSAGE</button>
                </form>
            </div>
        </div>
    );
}

export default Contacts;
